.card {
    border: 1px solid #f3f3f3;
    padding: 30px;
    width: 220px;
    border-radius: 40px;
    margin-right: 30px;
    margin-bottom: 20px;
    transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
    &:hover {
        box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
        transform: translateY(-5px);
    }
    .favourite {
        position: absolute;
        cursor: pointer;
    }
    span {
        text-transform: uppercase;
        font-size: 12px;
        opacity: .5;
    }
    b {
        font-size: 13px;
    }
    h5 {
        font-weight: 400;
        font-size: 14px;
    }
}

.plus {
    cursor: pointer;
}